/*////// Generals///////////////////////////////////////////// */
body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

h1,
h2 {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  display: block;
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  color:white;
}

.container {
  background: linear-gradient(45deg, #0d0d3a 10%, #000000 100%);
}

.clic {
  cursor: pointer;
}

