/* ///////TOOLBAR///////////////////////////////// */

.toolbar {
  position: fixed;
  width: 100%;
  display: block;
  z-index: 10;
  transition: transform 0.3s ease-out;
  transform: translateY(0); 
}

.toolbar.hidden {
  transform: translateY(-100%); 
}

.toolbar-content {
  width: 100%;
  height: 50px;
  background: #333333;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.toolbar-left-item {
  padding: 4em;
}

.toolbar-right-item {
  display: flex;
  padding: 1em;
}

.toolbar-right-item p {
  padding: .4em;
}

.toolbar-bottom-line {
  position: relative;
  height: 5px;
  background: linear-gradient(to right,#1a0b52 1%, #0d10b8 50%, #1a0b52 100%);
  overflow: hidden;
}

.toolbar-bottom-line::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 50%;
  height: 100%;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 1%,
    rgba(79, 120, 197, 0.8) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  animation: shine 3s infinite;
}

