/* ///////HOME///////////////////////////// */

.title-content {
  display: flex;
  min-height: var(--h);
  font-family: Arial;
  font-size: clamp(var(--font-min), 3vw, var(--font-max));
}

.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: var(--h);
  font-family: Arial;
  font-size: clamp(var(--min), 3vw, var(--max));
}

/* block */
.block-effect {
  display: flex;
}

.point {
  opacity: 0;
  transition: opacity 1s ease;
}

.point.visible {
  opacity: 1;
}

.hidden {
  opacity: 0;
}

.block-reveal {
  opacity: 1;
  --t: calc(var(--td) + var(--d));
  display: flex;
  color: transparent;
  padding-top: 30px;
  position: relative;
  overflow: hidden;
  animation: revealBlock 0s var(--t)  forwards;
}

.block-reveal::after {
  content: '';

  width: 0%;
  height: 100%;
  padding-bottom: 4px;

  position: absolute;
  top: 0;
  right: 0;

  background: var(--bc);
  animation: revealingIn var(--td) var(--d)  forwards, revealingOut var(--td) var(--t) forwards;
}


/* animations */
@keyframes revealBlock {
  100% {
    color: #fafafa;
  }
}

@keyframes revealingIn {
  0% {
    width: 0;
    transform: translateX(100%); /* Comenzamos desde la derecha */
  }

  100% {
    width: 100%; /* Terminamos con el ancho al 100% */
    transform: translateX(0); /* Terminamos en la posición inicial */
  }
}

@keyframes revealingOut {
  0% {
    transform: translateX(0); /* Comenzamos en la posición inicial */
  }
  50% {
    overflow: hidden;
  }
  100% {
   
    transform: translateX(-100%);
  }
}
/* //////////////////////////////////////////////////////////////// */
.block-reveal-2 {
  opacity: 1;
  --t: calc(var(--d) + var(--td));
  display: flex;
  color: transparent;
  padding-top: 30px;
  position: relative;
  overflow: hidden;
  animation: revealBlock2 0s var(--t) forwards;
}

.block-reveal-2::after {
  content: '';
  width: 0%;
  height: 100%;
  padding-bottom: 4px;
  position: absolute;
  top: 0;
  left: 0;
  background: var(--bc);
  animation: revealingIn2 var(--td) var(--d) forwards, revealingOut2 var(--td) var(--t) forwards;
}


/* animations */
@keyframes revealBlock2 {
  100% {
    color: #fafafa;
  }
}

@keyframes revealingIn2 {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

@keyframes revealingOut2 {
  0% {
    transform: translateX(0); 
  }
  100% {
    transform: translateX(105%); 
  }
}

/* Ball bouncing */
.ball-bouncing {
  opacity: 0;
  transition: opacity 4s ease;
  background-color: var(--lemon-chiffon);
}

.ball-bouncing::before {
  animation: shadowBouncing 1.2s ease-in infinite;
  background-color: rgb(255, 255, 255);
  border-radius: 1rem;
  bottom: .4rem;
  right: .6rem;
  content: '';
  height: 0.12rem;
  position: absolute;
  width: 1.2rem;
}

.ball {
  right: .4rem;
  background-color: var(--color);
  border-radius: 50%;
  height: 1.5rem;
  position: absolute;
  width: 1.5rem;
}

.ball-bouncing .ball  {
  animation: ballBouncing 1.2s ease-in infinite;
  bottom: .8rem;
  transform-origin: bottom;
}

.ball-bouncing.visible {
  opacity: 1;
}

@keyframes ballBouncing {
  0%,
  100% {
    transform: scale(1.2, 0.2);
    
  }

  20% {
    transform: scaleY(1.2);
  }
  20%,
  80% {
    transform: translateY(-1.4rem);
  }
  60% {
    transform: translateY(-1.5rem);
  }
  90% {
    transform: translateY(20%);
  }
}

@keyframes shadowBouncing {
  0%,
  100% {
    transform: scale(2, 0.8);
  }
  20% {
    transform: scale(1.2, 0.8);
  }
  40%,
  80% {
    transform: scale(0.5, 0.2);
  }
  70% {
    transform: scale(0.5, 0.15);
  }
  90% {
    transform: scale(1.5, 0.6);
  }
}




.button {
  border: none;
  padding: 10px 20px;
  font-size: 1em;
  border-radius: 5px;
  text-align: center;
}

.button-green {
  background-color: green;
}

.button-green:hover {
  background-color: darkgreen;
}

.button-blue {
  background-color: blue;
}

.button-blue:hover {
  background-color: darkblue;
}

.botton {
  background: #000000;
  width: 100%;
  overflow: hidden;
}

.content-botton {
  display: flex;
  padding: 1em;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  box-sizing: border-box; 
}

.face {
  padding: 0px 0px 0px 10%;
  top:0%;
  width: 300px; 
  height: 300px; 
  max-width: calc(100% - 2em);
  object-fit: cover;
  /* transition: clip-path 0.3s ease-in-out; 
  clip-path: circle(100% at 50% 50%); */
}




.scroll-container {
  width: 50%;
  overflow-x: hidden; 
  cursor: grab; 
  border: 1px solid #ccc; 
  white-space: nowrap; 
  position: relative; 
}

.scroll-content {
  display: flex;
  width: max-content; 
  padding: 10px; 
}

.scroll-content img {
  display: inline-block; 
  margin-right: 10px;
  max-height: 200px;
  background-color: #000000;
  width: 300px;
  height: 300px;
}

.scroll-content img.left-edge::before,
.scroll-content img.right-edge::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 20px; 
  background: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
}

.scroll-content img.left-edge::before {
  left: 0;
}

.scroll-content img.right-edge::after {
  right: 0;
}





@keyframes shine {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}
